<template>
  <div>
    <div style="color: white; height: 30px;" class="hidden-md-and-up"></div>
    <el-row type="flex" justify="center">
      <!-- 博客目录 -->
      <el-col :lg="20" :md="23" :sm="23" :xs="24" class="menu-container">
        <el-row>
          <!-- 顶级分类 -->
          <el-col :lg="4" :md="6" :sm="6" :xs="24">
            <div class="nav-container">
              <el-menu @select="showTable" background-color="#f7f7f7">
                <el-menu-item index="1">
                  <span class="nav-text" slot="title">导航一</span>
                </el-menu-item>
                <el-menu-item index="2">
                  <span class="nav-text" slot="title">导航二</span>
                </el-menu-item>
                <el-menu-item index="3">
                  <span class="nav-text" slot="title">导航三</span>
                </el-menu-item>
              </el-menu>
            </div>
          </el-col>
          <!-- 次级目录 -->
          <el-col :lg="20" :md="18" :sm="18" :xs="24">
            <div class="table-container">
              <el-table :data="posts" @row-click="showPost" :row-style="tableRowStyle" :header-cell-style="renderHeader">
                <el-table-column prop="name" :label="title"></el-table-column>
                <el-table-column prop="date" width="100px"></el-table-column>
              </el-table>
            </div>
          </el-col>
        </el-row>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'Menu',
  mounted () {
  },
  data () {
    return {
      posts: [
        {
          name: 'GithubPages',
          date: '2019-01-01',
          index: 0
        }
      ],
      title: '顶级目录一'
    }
  },
  methods: {
    showTable (index, indexPath) {
      // console.log(index, indexPath)
    },
    showPost (row, event, column) {
      // console.log(row.name)
      this.$router.push({name: 'MyBlogs', params: {postIndex: row.index}})
    },
    renderHeader () {
      return 'font-size: 24px; font-weight: 600; color: black; background-color: #f7f7f7;'
    },
    tableRowStyle ({row, rowIndex}) {
      return 'background-color: #f7f7f7; color: black; cursor: pointer;'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  @import './../assets/css/menu.css';
</style>



