<template>
  <div>
    <div style="color: white; height: 30px;" class="hidden-md-and-up"></div>
    <el-row type="flex" justify="center">
      <el-col :lg="20" :md="23" :sm="23" :xs="24" class="content">
        <div v-html="compiledMarkdown"></div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import GithubPages from './posts/GithubPages.md'
var marked = require('marked')
export default {
  name: 'MyBlogs',
  mounted () {
    this.index = this.$route.params.postIndex
    // console.log(this.index)
    this.compileMarkdown(this.index)
  },
  data () {
    return {
      blogs: [
        {
          blog: GithubPages
        }
      ],
      index: '',
      compiledMarkdown: ''
    }
  },
  methods: {
    compileMarkdown (index) {
      // console.log(index)
      var rendererMD = new marked.Renderer()
      marked.setOptions({
        renderer: rendererMD,
        gfm: true,
        tables: true,
        breaks: false,
        pedantic: false,
        sanitize: false,
        smartLists: true,
        smartypants: false
      })
      this.compiledMarkdown = marked(this.blogs[index].blog)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  @import './../assets/css/myBlogs.css';
</style>



